
 h2{
     color: #FFA713;
     font-size: 24px;
}
.btn{
     font-weight: 800;
     font-size: 18px;
     letter-spacing: 1px;
     display: inline-block;
     padding:  10px 30px;
     border-radius: 50px;
     border:  2px solid #3498db;
     text-decoration: none;
     background-color: #FC2E00;
     background-image: #F4F200;
  background-image: -moz-linear-gradient(top, #fff 0%, #F95A36 100%); 
  background-image: -webkit-linear-gradient(top, #fff 0%,#F95A36 100%); 
  background-image: linear-gradient(to bottom, #fff 0%,#F95A36 100%); 
  background-size: 300px;
  background-repeat: no-repeat;
  background-position: 0%;
  -webkit-transition: background 400ms ease-in-out;
  transition: background 400ms ease-in-out;
}

.btn:hover{
     background-position: -500%;
  transition: background 600ms ease-in-out;
  color: #fff;
}

img{
     text-align: right;
     overflow: hidden;
          user-select: none;
          position: relative;
        
}


.Iam {
     padding: 1em 4em;
     font: normal 15px/25px Montserrat, sans-serif;
     color: #999;
     width: 90vw;
     margin: 0 auto;
     
   }
   .Iam p {
     height: 25px;
     float: left;
     margin-right: 0.3em;
   }
   .Iam b {
     float: left;
     overflow: hidden;
     position: relative;
     height: 25px;
   }
   .Iam .innerIam {
     display: inline-block;
     color: #e74c3c;
     position: relative;
     white-space: nowrap;
     top: 0;
     left: 0;
   
   
   /*animation*/
   -webkit-animation:move 5s;
      -moz-animation:move 5s;
       -ms-animation:move 5s;
        -o-animation:move 5s;
           animation:move 5s;
   /*animation-iteration-count*/
   -webkit-animation-iteration-count:infinite;
      -moz-animation-iteration-count:infinite;
       -ms-animation-iteration-count:infinite;
        -o-animation-iteration-count:infinite;
           animation-iteration-count:infinite;
   /*animation-delay*/
   -webkit-animation-delay:1s;
      -moz-animation-delay:1s;
       -ms-animation-delay:1s;
        -o-animation-delay:1s;
           animation-delay:2s;
   }
   @keyframes move{
   0%  { top: 0px; }
   20% { top: -25px; }
   40% { top: -50px; }
   60% { top: -75px; }
   80% { top: -100px; }
   }
   
   @-webkit-keyframes move {
       0%  { top: 0px; }
       20% { top: -50px; }
       40% { top: -100px; }
       60% { top: -150px; }
       80% { top: -200px; }
   }
   @-moz-keyframes move {
       0%  { top: 0px; }
       20% { top: -50px; }
       40% { top: -100px; }
       60% { top: -150px; }
       80% { top: -200px; }
   }
   @-o-keyframes move {
       0%  { top: 0px; }
       20% { top: -50px; }
       40% { top: -100px; }
       60% { top: -150px; }
       80% { top: -200px; }
   }
   @keyframes move {
       0%  { top: 0px; }
       20% { top: -25px; }
       40% { top: -50px; }
       60% { top: -75px; }
       80% { top: -100px; }
   }

.margin{
     margin-top: 100px;
}
.cards{
     box-shadow: 4px 4px 8px 0 rgba(0,0,0,0.2);
     border-radius:20px;
     transition:0.3s
}

.cards:hover{
     transition:0.3s;
     box-shadow: 8px 8px 16px 0 rgba(0,0,0,0.2),-8px -8px 16px 0 rgba(0,0,0,0.2);
}

.animated{
     animation: up-down 2s ease-in-out infinite alternate-reverse;
}

#back {
     
transform:'scaleY(-1)',
}

@keyframes up-down{
     0%{
          transform: translateY(10px);
     }
     100%{
          transform: translateY(-10px);
     }

     
}

@-webkit-keyframes up-down{
     0%{
          transform: translateY(10px);
     }
     100%{
          transform: translateY(-10px);
     }

     
}

